body.wadi-addons-for-elementor_page_wadi-settings {
  font-size: 16px;
}
body.wadi-addons-for-elementor_page_wadi-settings h2, body.wadi-addons-for-elementor_page_wadi-settings h3 {
  background: #c9fff6;
  padding: 1rem;
  border-left: 13px solid #00c9a9;
}
body.wadi-addons-for-elementor_page_wadi-settings .wadi_paypal_client_id {
  width: 100%;
  font-size: 22px;
}
body.wadi-addons-for-elementor_page_wadi-settings .wadi_paypal_client_id input {
  font-size: inherit;
  width: 100%;
  margin: 18px 0;
}
body.wadi-addons-for-elementor_page_wadi-settings .wadi_paypal_currency {
  font-size: 22px;
}
body.wadi-addons-for-elementor_page_wadi-settings .wadi_paypal_currency select {
  font-size: inherit;
  display: block;
  margin: 18px 0;
}
body.wadi-addons-for-elementor_page_wadi-settings #wpcontent {
  padding-left: 0;
}
body.wadi-addons-for-elementor_page_wadi-settings h2 {
  font-size: 1.8rem;
}
body.wadi-addons-for-elementor_page_wadi-settings p {
  font-size: 1rem;
}
body.wadi-addons-for-elementor_page_wadi-settings .wrapper {
  margin: 0 auto;
  padding: 2rem;
  color: #000;
}
body.wadi-addons-for-elementor_page_wadi-settings .tabs {
  display: grid;
}
body.wadi-addons-for-elementor_page_wadi-settings [role=tablist] {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
  grid-gap: 10px;
}
body.wadi-addons-for-elementor_page_wadi-settings [role=tabpanel] {
  background: #ffffff;
  padding: 2rem;
}
body.wadi-addons-for-elementor_page_wadi-settings a[role=tab] {
  background: #ffffff;
  border: 0;
  color: black;
  cursor: pointer;
  font-size: 1.5rem;
  padding: 15px;
  height: 100px;
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;
}
body.wadi-addons-for-elementor_page_wadi-settings a[role=tab]:first-of-type {
  border-radius: 25px 0 0 25px;
}
body.wadi-addons-for-elementor_page_wadi-settings a[role=tab]:last-of-type {
  border-radius: 0 25px 25px 0;
}
body.wadi-addons-for-elementor_page_wadi-settings a[role=tab]:only-child {
  border-radius: 25px;
}
body.wadi-addons-for-elementor_page_wadi-settings .wadi_tabpanel {
  border-radius: 25px;
  margin-top: 20px;
}
body.wadi-addons-for-elementor_page_wadi-settings a[aria-selected=true] {
  background: #00c9a9;
  box-shadow: none;
  color: #fff;
}